//个人用户信息

import {LOGIN_FAILURE} from "../mutation_type"

const state={
	user:{
		success:false,
		errormsg:'',
		loginname:'',
		id:'',
		avatar_url:'',
	}
}
const mutations={
	//登录失败
	LOGIN_FAILURE(state,data){
		state.user.errormsg=data.error_msg;
		state.user.success=data.success;
		console.log(state)
	},
	//登录成功
	LOGIN_SUCCESS(state,data){
		state.user.success=data.success;
		state.user.loginname=data.loginname;
		state.user.id=data.id;
		state.user.avatar_url=data.avatar_url;
	},
	//注销成功,删除个人数据
	LOGOUT_SUCCESS(state,data){
		state.user={
			success:false,
			errormsg:'',
			loginname:'',
			id:'',
			avatar_url:'',
		}
	}
}

export default{
	state,mutations
}


